.bench-slot .champion {
  text-shadow: -1px -1px 0 #000,   1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000; }

.bench-slot .champion {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.bench {
  border: 2px solid black;
  padding: 0.5em;
  display: flex; }

.bench-slot {
  border: 2px solid black;
  border-radius: 5px;
  flex: 1;
  height: 5em;
  margin: 0.1em; }
  .bench-slot .champion {
    background-color: black;
    color: white;
    height: 100%;
    text-align: center; }
    .bench-slot .champion .champion-tier {
      font-size: 1.5em; }
